
import { defineComponent } from 'vue';
import HomeView from './views/Home.view.vue';
import LoginView from './views/Login.view.vue';
import CadastroView from './views/Cadastro.view.vue';
import GradeView from './views/Grade.view.vue';
import AnotacoesView from './views/Anotacoes.view.vue';
import ConfiguracoesView from './views/Configuracoes.view.vue';

export default defineComponent({
  name: 'App',

  components: {
    HomeView,
    LoginView,
    CadastroView,
    GradeView,
    AnotacoesView,
    ConfiguracoesView
  },

  data(): {
    page: string | undefined,
    paginasSeguras: string[]
  } {
    return {
      page: undefined,
      paginasSeguras: ['Grade', 'Anotacoes', 'Configuracoes']
    }
  },

  methods: {
    goToPage(page: string) {
      this.page = page;
    },
    sair() {
      localStorage.removeItem('access_token');
      this.goToPage('Home');
    }
  },

  mounted() {

    let page = localStorage.getItem('page');
    if (page) {
      this.page = page;
    } else {
      localStorage.setItem('page', 'Home')
      this.page = 'Home'
    }

    this.axios.interceptors.request.use(
      config => {
        let access_token = localStorage.getItem('access_token');
        if (access_token)
          config.headers.Authorization = `Bearer ${access_token}`;

        return config;
      },
      error => Promise.reject(error)
    )

    this.axios.interceptors.response.use(
      value => value,
      error => {
        if (error.response.status == 422 || error.response.status == 401) {
          localStorage.removeItem('access_token');
          this.goToPage('Login')
        }

        return Promise.reject(error);
      }
    )
  },

  watch: {
    page(newPage: string) {
      localStorage.setItem('page', newPage);
    }
  }

});

